@keyframes rotate-heartbeat {
  0% {
    transform: scale(1) rotate(0deg);
  }
  50% {
    transform: scale(1.5) rotate(180deg);
  }
  100% {
    transform: scale(1) rotate(360deg);
  }
}
.playlist__item {
  width: 100%;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  transition: all 140ms ease-in-out;
  &:hover {
    cursor: pointer;
    background-color: #1f1f1f;
  }
}

.track__title_svg {
  width: 50px;
  height: 50px;
  fill: transparent;
}

.track__author {
  color: #696969;
}

.track__album {
  color: #696969;
}

.track__time_svg {
  width: 14px;
  height: 12px;
  margin-right: 17px;
  fill: transparent;
  stroke: #696969;
}

.track__time_text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #696969;
}

.track__time {
  display: flex;
  align-items: center;
  gap: 10px;
}

.light {
  background: transparent;
  color: black;
}

.dark {
  background: transparent;
  color: white;
}
.likeIcon {
  cursor: pointer;
  transition: all 140ms ease-in-out;
  &:hover {
    transform: scale(1.3);
  }
}

.track__title_text {
  color: #696969;
}

.pos {
  width: 240px;
}
.trackImg {
  width: 38px;
  height: 38px;
  background-color: #313131;
  border-radius: 50%;
  display: flex;
  align-items: center;
}
.melodySvg {
  padding: 10px;
}
.rotate_center {
  animation: rotate-heartbeat 4s linear infinite;
}
.target {
  cursor: pointer;
  background-color: #1f1f1f;
}
