.centerblock__search {
  margin-top: 10px;
  width: 100%;
  border-bottom: 1px solid #4e4e4e;
  margin-bottom: 31px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.search__text::-webkit-input-placeholder {
  background-color: transparent;
  color: #ffffff;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.search__text::placeholder {
  background-color: transparent;
  color: #ffffff;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.search__svg {
  width: 17px;
  height: 17px;
  margin-right: 5px;
  fill: transparent;
}
.search__text {
  -webkit-box-flex: 100;
  -ms-flex-positive: 100;
  flex-grow: 100;
  background-color: transparent;
  border: none;

  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.search__text:-webkit-autofill,
.search__text:-webkit-autofill:hover,
.search__textt:-webkit-autofill:focus,
.search__text:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-text-fill-color: #fff !important;
}
.search__text:focus {
  outline: none;
}

.light::placeholder {
  color: black;
  background-color: transparent;
}

.dark::placeholder {
  color: white;
  background-color: transparent;
  fill: black;
}
